import React from 'react';
import './Notes.css';

export function Section1(props) {
    return (
        <section className="Notes-section">
        <p>These are some rough notes on Marx, presented with an aim to contextualise some of his equations.</p>
        <p>Marx's Capital is a critique of existing economic theories. He is influenced in particular by J.S. Mill, Smith &amp; Ricardo, and heavily critical of Malthus, Bentham &amp; others. His approach emphasises the need to take the system as a whole, rather than analysing only partial elements removed from context. Unlike his contemporaries, Marx puts a lot of work into establishing the history and development of the phenomena he analyses, going beyond their superfical, present-day form to investigate how they change. He investigates key characteristics of each historical mode of production in Europe, and explains how each transitioned into the next.</p>
        <p>Capital is not present throughout history, and economic activity is more than just a series of exchanges. Production and distribution are related. Private property determines distribution. Relations of production shape the legal &amp; cultural system. Unique to capitalism is the emergence of labour as a commodity. As a commodity, it has an apparent equality with respect to buyers and sellers. The buyers pay a given price without distinction as to the political character of the labour recieved. The same labour can be sold to one buyer or another for the same price. This "equality" is capitalist "freedom".</p>
        <p>Marx identifies the dual character of commodities - the separation of use value (an object's useful purpose) and exchange value (the proportion at which the commodity can be exchanged for another). Money develops out of the need for a universal equivalent, and in particular circumstances money forms part of Capital. All commodities are produced by human labour. Use values are qualitative, and can't be compared directly - the usefulness of a coat can't be measured in terms of how useful a chair is, and so use is an unsuitable basis for exchange value. Exchange values must quantitative and thus comparable - in a given time and place, coats and chairs may be exchanged for each other in a given proportion. This proportion is determined by the average quantity of labour necessary for the production of each commodity.</p>
        <p>The Marxist theory of value diverges from other economists. Value corresponds to the labour socially embedded in a commodity, every other consideration is secondary. Marx discusses 'useful labour', which is labour that produces particular use values - the production of things in order to use them. In contrast with 'abstract labour', which is labour that produces exchange values - the production of things in order to sell them. A commodity is anything that is to be exchanged - not used immediately by those involved in its production. Workers purchasing commodities from capitalists leads to the capitalists' economic power becoming greater than the economic power of the workers. A commodity producing society must:</p>
        
        <ol>
          <li>Specialise</li>
          <li>Seperate use value from exchange value (by producing for exchange &amp; fulfilling use values by acquiring commodities from other producers)</li>
          <li>Have an extensive, well developed market utilising money as a means of exchange</li>
        </ol>
        
        <p>The social relationship among producers is characterised as follows: the producer's livelihood depends on commodities, but the value is not controlled by producers but by the economic relations of the whole society expressed through the market. Thus relations between producers appear to be relations between things. This produces a false belief in value being created by exchange.</p>
        <p>The exchange of commodities can be viewed from two perspectives. <span className="Notes-formula">C→M→C</span> represents selling a commodity in order to buy another commodity. <span className="Notes-formula">M→C→M'</span> represents buying a commodity in order to sell it, transforming money into commodities to make more money. In the sphere of circulation, <span className="Notes-formula">M→C→M'</span> is the general formula for <span className="Notes-term">Capital</span>. The difference between M and M' is <span className="Notes-term">surplus value</span>. The extraction of surplus value is what drives capitalism. Capital is personified in the capitalist, who is compelled to obey the laws of capital. Acquiring use values is not the aim of a capitalist - the sole aim is profit. Profit making is a never ending process.</p>
        <p>The market does not determine value. In particular, the market is not the source of value. The relations between all buyers and sellers, represented in the market, is a window through which we can view the totality of the relations of a given society that together determine the socially-average quantity of labour necessary for the production of a commodity. It is these relations, and the socially-average necessary quantity of labour in particular, that determine a commodity's value. Labour is the source of all value.</p>
        <p>The circulation of capital follows three stages. In the first stage, <span className="Notes-formula">M→C</span>, the capitalist acquires commodities in the market - a <span className="Notes-term">'circulation act'</span>. In the second stage, <span className="Notes-formula">P</span>, the purchased commodities are consumed productively, resulting in a commodity of more value than the commodity inputs. In the third stage, <span className="Notes-formula">C→M</span>, the capitalist returns to the market and transforms the commodity back into money in another circulation act.</p>
        <p>Marx's formula for the whole circuit is <span className="Notes-formula">M→C…P…C'→M'</span>. The ellipsis indicates that the process of circulation is interrupted, and <span className="Notes-formula">C' &amp; M'</span> designate <span className="Notes-formula">C &amp; M</span> increased by <span className="Notes-term">surplus-value</span>. </p>
        <p>Labour is found as a commodity on the market when the possessors of labour power put it up for sale. Labour is sold when the sellers have nothing else left to sell, as owning no means of production or subsistance leaves the labourer without a way to exchange commodities for necessary goods, nor to survive or reproduce alone. In this unnatural condition, the labourer has two choices - starve, or sell labour power. Like any commodity, the value of labour power (wages) is determined by the value needed to produce it. For the continuous reproduction of the labourer, this is the value necessary for subsistance, such as food, water, shelter and payment of debts. For labour that requires some degree of education, the value of subsistance incorporates the value of the education. Unique to labour power as a commodity, unlike any other commodity, is that its employment can add more value than it takes to reproduce - this is why it is termed <span className="Notes-term"> variable capital</span>. In contrast, all other commodities involved in the production process — the <span className="Notes-term">means of production</span> — are termed <span className="Notes-term">constant capital</span>. </p>
        <p>For example, imagine in a particular job it takes 4 hours of labour to produce the value equivalent to the labourers's daily reproduction. This is <span className="Notes-term">necessary labour</span>. Any additional labour beyond this is <span className="Notes-term">surplus labour</span>. </p>
        </section>
    );
}

